export default {
	data() {
		return {
			tabList: [
				{ id: 1, text: '我的会员', to: '' },
				{ id: 2, text: '学分记录', to: '' },
			],
			tabClick:0,
			option: [
				{ id: 1, quest: '免费观看所有在线教育视频' },
				{ id: 1, quest: '免费观看所有在线教育视频' },
				{ id: 1, quest: '免费观看所有在线教育视频' },
				{ id: 1, quest: '免费观看所有在线教育视频' },
			],
			wdxf:0,
			classList: [
				{ id: 1, quest: '完整观看《职业素养的培训》课程', jingyan: '8' },
				{ id: 2, quest: '完整观看《职业素养的培训》课程', jingyan: '80' },
				{ id: 3, quest: '完整观看《职业素养的培训》课程', jingyan: '6.5' },
				{ id: 4, quest: '完整观看《职业素养的培训》课程', jingyan: '2' },
			],
			user_token:''
		}
	},
	created(){
		
	},
	onLoad() {
		uni.getStorage({
		    key: 'user_token',
		    success: (res)=> {
					console.log(res)
		      this.user_token = res.data;
		    }
		});
		// this.getBanner();
		 this.getsuccess();
		
	},
	methods:{
		tabChange (e) {
			this.tabClick = e
		},
		switchTab (e) {
			this.tabClick = e.detail.current
		},
		getsuccess(){
			uni.request({
				url: this.$url+'api/user/mycredits'+this.user_token,
				method: 'post',
				data: {},
				success: res => {
					console.log(res);
					if(res.data.code == 10000){
						this.classList = res.data.data.list;
						this.wdxf = res.data.data.wdxf;
					}else{
						this.$msg(res.data.msg)	
					}
				},
			})
		},
	},
}
